body {
  position: relative;
  min-width: 320px;
  margin: 0;
  font-size: 16px;
  font-weight: 400;
  font-family: "Roboto", "Arial", sans-serif;
  transition: 1s;
}
body::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
body::-webkit-scrollbar-track {
  background-color: transparent;
}
body::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #6320EE;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-display: swap;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/roboto-300.woff2") format("woff2"), url("../fonts/roboto-300.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/roboto-700.woff2") format("woff2"), url("../fonts/roboto-700.woff") format("woff");
}
.dark .button:hover, .dark .button:focus {
  box-shadow: 0 0 10px 5px rgba(1, 151, 246, 0.9);
}
.dark .button:focus {
  outline-color: #0197F6;
}
.dark .button:active {
  box-shadow: 0 0 10px 2px #0197f6;
}
.dark .control__range::-webkit-slider-runnable-track {
  box-shadow: 0 0 3px 1px rgba(1, 151, 246, 0.9);
}
.dark .control__range::-webkit-slider-thumb {
  background-color: #0197F6;
}
.dark .control__icon {
  fill: #0197F6;
}
.dark .footer__theme--active {
  box-shadow: 0 0 5px 1px rgba(1, 151, 246, 0.6);
}
.dark .footer__icon {
  fill: #0197F6;
  stroke: #0197F6;
}
.dark .footer__copyright {
  color: #0197F6;
}
.dark .footer__copyright--name {
  color: #FFFFFF;
}
.dark .header__logo path:first-of-type {
  fill: #0197F6;
}
.dark .header__logo path:last-of-type {
  fill: #FFFFFF;
}
.dark .header__icon {
  fill: #0197F6;
}
.dark .main {
  background-color: #02182B;
}
.dark .settings {
  background-color: rgba(2, 24, 43, 0.8);
}
.dark .settings__icon {
  fill: #0197F6;
}
.dark .video {
  background-color: #364652;
}
.dark .video__wrapper {
  box-shadow: 0 0 10px 3px rgba(1, 151, 246, 0.9);
}
.dark .video__icon path {
  stroke: #0197F6;
}
.dark .statistics {
  color: #0197F6;
}

.header {
  position: absolute;
  top: 0;
  left: 50%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  margin: 0 auto;
  width: 100%;
  padding: 25px;
  transform: translateX(-50%);
}

.header__logo {
  width: 150px;
  height: 45px;
}

.header__menu {
  width: 35px;
  height: 35px;
}
.header__menu--error {
  animation-name: shake;
  animation-duration: 0.1s;
  animation-iteration-count: 11;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  animation-direction: alternate;
}
.header__menu--error .header__icon {
  fill: rgb(243, 80, 80);
}

@keyframes shake {
  0% {
    transform: translateX(-10%);
  }
  100% {
    transform: translateX(0%);
  }
}
.header__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: #8075FF;
}

@media (min-width: 768px) {
  .header__logo {
    width: 180px;
  }
  .header__menu {
    width: 40px;
    height: 40px;
  }
  .header__icon {
    width: 30px;
    height: 30px;
  }
}
@media (min-width: 1440px) {
  .header__logo {
    width: 230px;
  }
  .header__menu {
    width: 50px;
    height: 50px;
  }
  .header__icon {
    width: 35px;
    height: 35px;
  }
}
.button {
  position: relative;
  box-sizing: border-box;
  display: block;
  margin: 0;
  padding: 0;
  font-family: inherit;
  border: none;
  border-radius: 5px;
  background-color: transparent;
  cursor: pointer;
  outline: none;
  transition: 0.2s;
}
.button:hover, .button:focus {
  box-shadow: 0 0 10px 1px rgba(99, 32, 238, 0.9);
}
.button:focus {
  outline: 1px solid #6320EE;
}
.button:active {
  box-shadow: 0 0 10px 2px #6320ee;
}

.settings {
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 20px 25px;
  color: #FFFFFF;
  text-align: center;
  background-color: rgba(128, 117, 255, 0.5);
  backdrop-filter: blur(5px);
  overflow-y: scroll;
  transition: 0.6s;
  z-index: 2;
}
.settings--hide {
  opacity: 0;
  transform: translateY(-100%);
}

.settings__close {
  position: absolute;
  top: 25px;
  right: 25px;
  width: 35px;
  height: 35px;
  z-index: 3;
}

.settings__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: #6320EE;
}

.settings__column {
  margin-bottom: 30px;
}
.settings__column:last-child {
  margin-bottom: 0;
}

.settings__summary {
  display: inline-block;
  cursor: pointer;
}
.settings__summary::-webkit-details-marker {
  display: none;
}

.settings__label {
  display: block;
  margin-bottom: 5px;
  cursor: pointer;
}
.settings__label:last-child {
  margin-bottom: 0;
}

.settings__checkbox:disabled {
  cursor: not-allowed;
}

.settings__name {
  font-size: 15px;
  font-weight: 300;
}

.settings__sub {
  margin: 0;
  margin-bottom: 20px;
  font-size: 25px;
  font-weight: 700;
}

.settings__text {
  margin-top: 0;
  margin-bottom: 15px;
  font-weight: 300;
}

.settings__list {
  margin: 0;
  padding: 0;
}

.settings__item {
  margin-bottom: 8px;
  font-weight: 300;
  list-style: inside;
}
.settings__item:last-child {
  margin-bottom: 0;
}

.settings__button {
  width: fit-content;
  margin: 0 auto;
  margin-bottom: 15px;
  padding: 10px 15px;
  color: #FFFFFF;
  font-size: 16px;
  font-weight: 300;
  border: 1px solid #FFFFFF;
  border-radius: 30px;
}
.settings__button:last-child {
  margin-bottom: 0;
}

.settings__file {
  display: inline-block;
  max-width: 160px;
  margin: 0 auto;
  padding: 10px 15px;
  color: #FFFFFF;
  font-size: 16px;
  font-weight: 300;
  font-family: inherit;
  border: 1px solid #FFFFFF;
  border-radius: 30px;
}
.settings__file::-webkit-file-upload-button {
  display: none;
}

@media (min-width: 768px) {
  .settings {
    display: grid;
    grid-template-columns: 400px 400px;
    grid-gap: 0 30px;
    overflow-y: auto;
  }
  .settings__column {
    margin-bottom: 0;
  }
  .settings__column:last-of-type {
    grid-column-start: 1;
    grid-column-end: 3;
  }
  .settings__close {
    width: 40px;
    height: 40px;
  }
  .settings__icon {
    width: 30px;
    height: 30px;
  }
  .settings__label {
    margin-bottom: 15px;
  }
  .settings__name {
    font-size: 18px;
  }
  .settings__sub {
    font-size: 30px;
  }
  .settings__text {
    font-size: 18px;
  }
  .settings__item {
    font-size: 18px;
  }
  .settings__button {
    padding: 15px 25px;
    font-size: 18px;
  }
  .settings__file {
    max-width: 185px;
    padding: 15px 20px;
    font-size: 18px;
  }
}
@media (min-width: 1440px) {
  .settings__close {
    width: 50px;
    height: 50px;
  }
  .settings__icon {
    width: 40px;
    height: 40px;
  }
  .settings__label {
    margin-bottom: 20px;
  }
  .settings__name {
    font-size: 22px;
  }
  .settings__sub {
    margin-bottom: 30px;
    font-size: 35px;
  }
  .settings__text {
    font-size: 20px;
  }
  .settings__item {
    font-size: 20px;
  }
  .settings__button {
    font-size: 22px;
  }
  .settings__file {
    max-width: 215px;
    font-size: 22px;
  }
}
.main {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  color: #FFFFFF;
  background-color: #F8F0FB;
}

.video {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  background-color: rgb(245, 246, 247);
  object-fit: cover;
}

.video__wrapper {
  position: relative;
  box-sizing: border-box;
  width: 90%;
  max-width: 500px;
  box-shadow: 0 0 10px 3px rgba(99, 32, 238, 0.5);
  border-radius: 10px;
  overflow: hidden;
}
.video__wrapper:hover .control {
  transform: translateY(0);
}
.video__wrapper:hover .statistics {
  transform: translateY(0);
}

.video__start {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  outline-style: inset;
  outline-offset: -2px;
  border-radius: 10px;
  transition: 0.2s;
  z-index: 1;
}
.video__start--hide {
  background-color: transparent;
  opacity: 0;
  z-index: -1;
}
.video__start::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(99, 32, 238, 0.2);
}
.video__start:hover, .video__start:focus {
  box-shadow: none;
}
.video__start:hover .video__icon, .video__start:focus .video__icon {
  transform: translate(-50%, -50%) scale(1.2);
}
.video__start:hover .video__icon path, .video__start:focus .video__icon path {
  opacity: 1;
}
.video__start:active .video__icon {
  transform: translate(-50%, -50%) scale(0.9);
}

.video__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.2s;
}
.video__icon path {
  opacity: 0.6;
  stroke: #8075FF;
  transition: 0.2s;
}

@media (min-width: 768px) {
  .video__wrapper {
    width: 500px;
    height: 300px;
  }
  .video__icon {
    width: 150px;
    height: 150px;
  }
}
@media (min-width: 1440px) {
  .video__wrapper {
    width: 900px;
    max-width: max-content;
    height: 505px;
  }
  .video__icon {
    width: 170px;
    height: 170px;
  }
}
.statistics {
  position: absolute;
  top: 0;
  right: 0;
  box-sizing: border-box;
  padding: 10px 15px;
  color: #8075FF;
  border-top-right-radius: 10px;
  border-bottom-left-radius: 10px;
  background-color: rgba(33, 26, 29, 0.5);
  transition: 0.6s;
  transform: translateY(-100%);
}
.statistics--hide {
  opacity: 0;
  transform: translateY(0);
  z-index: -1;
}
.statistics--off {
  display: none;
}

.statistics__category {
  margin: 0;
  margin-bottom: 5px;
  font-size: 10px;
}
.statistics__category:last-child {
  margin-bottom: 0;
}
.statistics__category span {
  color: #FFFFFF;
  font-weight: 300;
}

@media (min-width: 768px) {
  .statistics__category {
    font-size: 13px;
  }
}
@media (min-width: 1440px) {
  .statistics {
    padding: 20px 30px;
  }
  .statistics__category {
    margin-bottom: 10px;
    font-size: 18px;
  }
}
.control {
  position: absolute;
  bottom: 0;
  left: 0;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 10px;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
  background-color: rgba(33, 26, 29, 0.5);
  transform: translateY(100%);
  transition: 0.6s;
  z-index: 1;
}
.control--hide {
  opacity: 0;
  transform: translateY(0);
  z-index: -1;
}
.control:focus-within {
  transform: translateY(0);
}

.control__button {
  width: 30px;
  height: 30px;
  margin-right: 15px;
}
.control__button:last-child {
  margin-right: 0;
}

.control__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: #6320EE;
}
.control__icon--hide {
  display: none;
}

.control__mute {
  opacity: 0;
}

.control__range {
  appearance: none;
  position: relative;
  flex-grow: 1;
  height: 5px;
  margin-right: 15px;
  border-radius: 5px;
}
.control__range::-webkit-slider-runnable-track {
  width: 100%;
  height: 5px;
  box-shadow: 0 0 3px 1px rgba(99, 32, 238, 0.9);
  border-radius: 5px;
  background-color: #FFFFFF;
}
.control__range::-webkit-slider-thumb {
  appearance: none;
  width: 15px;
  height: 15px;
  margin-top: -6px;
  border-radius: 50%;
  background-color: #6320EE;
  cursor: pointer;
}
.control__range::-moz-range-track {
  appearance: none;
  width: 100%;
  height: 3px;
  box-shadow: 0 0 3px 1px rgba(99, 32, 238, 0.9);
  border-radius: 5px;
  background-color: #FFFFFF;
  cursor: pointer;
}
.control__range::-moz-range-thumb {
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #6320EE;
  cursor: pointer;
}

@media (min-width: 768px) {
  .control {
    padding: 15px;
  }
  .control__button {
    width: 40px;
    height: 40px;
  }
  .control__icon {
    width: 25px;
    height: 25px;
  }
  .control__range {
    height: 8px;
  }
  .control__range::-webkit-slider-runnable-track {
    height: 8px;
  }
  .control__range::-webkit-slider-thumb {
    width: 20px;
    height: 20px;
  }
}
.footer {
  position: absolute;
  bottom: 0;
  left: 50%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  margin: 0 auto;
  width: 100%;
  padding: 25px;
  transform: translateX(-50%);
}

.footer__controls {
  display: flex;
  align-items: center;
}

.footer__theme {
  width: 35px;
  height: 35px;
  margin-right: 15px;
}
.footer__theme:last-child {
  margin-right: 0;
}
.footer__theme--active {
  box-shadow: 0 0 5px 1px rgba(99, 32, 238, 0.6);
  cursor: not-allowed;
}

.footer__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: #8075FF;
  stroke: #8075FF;
}

.footer__copyright {
  color: #8075FF;
  font-size: 15px;
  font-weight: 300;
  text-align: right;
}
.footer__copyright--name {
  padding: 0;
  font-size: inherit;
  border: none;
  background-color: transparent;
  cursor: not-allowed;
}

@media (min-width: 768px) {
  .footer__theme {
    width: 40px;
    height: 40px;
  }
  .footer__icon {
    width: 30px;
    height: 30px;
  }
  .footer__copyright {
    font-size: 16px;
  }
}
@media (min-width: 1440px) {
  .footer__theme {
    width: 50px;
    height: 50px;
  }
  .footer__icon {
    width: 35px;
    height: 35px;
  }
  .footer__copyright {
    font-size: 18px;
  }
}