.button {
  position: relative;
  box-sizing: border-box;
  display: block;
  margin: 0;
  padding: 0;
  font-family: inherit;
  border: none;
  border-radius: 5px;
  background-color: transparent;
  cursor: pointer;
  outline: none;
  transition: 0.2s;

  &:hover,
  &:focus {
    box-shadow: 0 0 10px 1px rgba(#6320EE, 0.9);
  }

  &:focus {
    outline: 1px solid #6320EE;
  }

  &:active {
    box-shadow: 0 0 10px 2px rgba(#6320EE, 1);
  }
}

